/* table style */
.container__table__list {
  display: grid;
  grid-template-columns: 96%;
  justify-content: center;
}

/* size table*/
.container__table__list > table {
  display: grid;
  grid-template-columns: 100%;
}
.container__table__list > table > thead {
  display: grid;
  grid-template-columns: 100%;
}
.container__table__list > table > thead > tr {
  display: grid;
  grid-template-columns: 6% 18.8% 18.8% 18.8% 18.8% 18.8%;
}

@media all and (max-width: 940px) {
  .container__table__list > table > thead > tr {
    grid-template-columns: 33% 33% 34%;
  }
  .container__table__list > table > thead > tr > th:nth-child(3) {
    border-left: none !important;
  }
  .container__table__list > table > thead > tr > th:nth-child(5) {
    border-top: 1px solid #00000090;
  }
  .container__table__list > table > thead > tr > th:nth-child(4) {
    border-top: 1px solid #00000090;
  }
  .container__table__list > table > thead > tr > th:nth-child(6) {
    border-top: 1px solid #00000090;
  }
}

.container__table__list > table {
  display: grid;
  grid-template-columns: 100%;
}
.container__table__list > table > tbody {
  display: grid;
  grid-template-columns: 100%;
}
.container__table__list > table > tbody > tr {
  display: grid;
  grid-template-columns: 6% 18.8% 18.8% 18.8% 18.8% 18.8%;
}
