/*----Select Group-----*/
.selectgroup {
	display: inline-flex;
}
.selectgroup-item {
	flex-grow: 1;
	position: relative;
}
.selectgroup-item+.selectgroup-item {
	margin-left: -1px;
}
.selectgroup-item:not(:first-child) .selectgroup-button {
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;
}
.selectgroup-item:not(:last-child) .selectgroup-button {
	border-top-right-radius: 0;
	border-bottom-right-radius: 0;
}
.selectgroup-input {
	opacity: 0;
	position: absolute;
	z-index: -1;
	top: 0;
	left: 0;
}
.selectgroup-button {
	display: block;
	border: 1px solid rgba(107,122,144,0.1);
	text-align: center;
	padding: 0.375rem 1rem;
	position: relative;
	cursor: pointer;
	border-radius: 3px;
	color: #cbd2dc;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	font-size: 0.9375rem;
	line-height: 1.5rem;
	min-width: 2.375rem;
}
.selectgroup-button-icon {
	padding-left: .5rem;
	padding-right: .5rem;
	font-size: 1rem;
}
.selectgroup-input:checked+.selectgroup-button {
	border-color: #1753fc ;
	z-index: 1;
	color: #1753fc ;
	background: #f4f3f9;
}
.selectgroup-input:focus+.selectgroup-button {
	border-color: #1753fc ;
	z-index: 2;
	color: #1753fc ;
	box-shadow: 0 0 0 2px rgba(70, 127, 207, 0.25);
}
.selectgroup-pills {
	flex-wrap: wrap;
	align-items: flex-start;
}
.selectgroup-pills .selectgroup-item {
	margin-right: .5rem;
	flex-grow: 0;
}
.selectgroup-pills .selectgroup-button {
	border-radius: 50px !important;
}
/*----Select Group-----*/

/*----Custom Switch-----*/
.custom-switch {
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	cursor: default;
	display: inline-flex;
	align-items: center;
	margin: 0;
}
.custom-switch-input {
	position: absolute;
	z-index: -1;
	opacity: 0;
}
.custom-switches-stacked {
	display: flex;
	flex-direction: column;
}
.custom-switches-stacked .custom-switch {
	margin-bottom: .5rem;
}
.custom-switch-indicator {
	display: inline-block;
	height: 1.25rem;
	width: 2.25rem;
	background: #e9ecef;
	border-radius: 50px;
	position: relative;
	vertical-align: bottom;
	border: 1px solid rgba(107,122,144,0.1);
	transition: .3s border-color, .3s background-color;
}
.custom-switch-indicator:before {
	content: '';
	position: absolute;
	height: calc(1.25rem - 4px);
	width: calc(1.25rem - 4px);
	top: 1px;
	left: 1px;
	background: #fff;
	border-radius: 50%;
	transition: .3s left;
	box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.4);
}
.custom-switch-input:checked~.custom-switch-indicator {
	background: #1753fc;
}
.custom-switch-input:checked~.custom-switch-indicator:before {
	left: calc(1rem + 1px);
}
.custom-switch-input:focus~.custom-switch-indicator {
	box-shadow: 0 0 0 2px rgba(70, 127, 207, 0.25);
	border-color:#1753fc ;
}
.custom-switch-description {
	margin-left: .5rem;
	color: #6e7687;
	transition: .3s color;
}
.custom-switch-input:checked~.custom-switch-description {
	color: #495057;
}
.material-switch > input[type="checkbox"] {
    display: none;   
}
.material-switch > label {
    cursor: pointer;
    height: 0px;
    position: relative; 
    width: 40px;  
}
.material-switch > label::before {
    background: rgb(0, 0, 0,0.2);
    box-shadow: inset 0px 0px 10px rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    content: '';
    height: 16px;
    margin-top: -8px;
    position:absolute;
    opacity: 0.3;
    transition: all 0.4s ease-in-out;
    width: 40px;
}
.material-switch > label::after {
    background: rgb(255, 255, 255);
    border-radius: 16px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
    content: '';
    height: 24px;
    left: -4px;
    margin-top: -8px;
    position: absolute;
    top: -4px;
    transition: all 0.3s ease-in-out;
    width: 24px;
}
.material-switch > input[type="checkbox"]:checked + label::before {
    background: inherit;
    opacity: 0.5;
}
.material-switch > input[type="checkbox"]:checked + label::after {
    background: inherit;
    left: 20px;
}
/*----Custom Switch-----*/

/*----Labels-----*/
.label-default{
	background:#868e96;
}
.label-primary{
	background:#1753fc ;
}
.label-success{
	background:#2fd8c6;
}
.label-danger{
	background:#ec2d38 ;
}
.label-warning{
	background:#fdb901;
}
.label-info{
	background:#28afd0;
}
/*----Labels-----*/

