	font-size: 0.9375rem;
}
.alert-icon {
	padding-left: 3rem;
}
.alert-icon>i {
	color: inherit !important;
	font-size: 1rem;
	position: absolute;
	top: 1rem;
	left: 1rem;
}
.alert-avatar {
	padding-left: 3.75rem;
}
.alert-avatar .avatar {
	position: absolute;
	top: .5rem;
	left: .75rem;
}
.close {
	font-size: 1rem;
	line-height: 1.5;
	transition: .3s color;
}
