/*----- Date Picker ----*/
.ui-datepicker {
	background-color: #f4f3f9;
	border: 1px solid #ced4da;
	font-family: inherit;
	font-size: inherit;
	padding: 10px;
	margin: 1px 0 0;
	display: none;
	width: auto !important;
	z-index: 5 !important;
}
.ui-datepicker .ui-datepicker-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;
	font-weight: 500;
	font-size: 12px;
	text-transform: uppercase;
	color: #473b52;
	padding: 0 0 5px;
	letter-spacing: 1px;
	border: 0;
	background-color: transparent;
	border-top-left-radius: 3px;
	border-top-right-radius: 3px;
}
.ui-datepicker .ui-datepicker-header .ui-datepicker-next, .ui-datepicker .ui-datepicker-header .ui-datepicker-prev {
	text-indent: -99999px;
	color: #2e384d;
	top: 1px;
}
.ui-datepicker .ui-datepicker-header .ui-datepicker-next::before, .ui-datepicker .ui-datepicker-header .ui-datepicker-prev::before {
	font-size: 16px;
	font-family: 'FontAwesome';
	position: absolute;
	top: -4px;
	text-indent: 0;
}
.ui-datepicker .ui-datepicker-header .ui-datepicker-next:hover::before, .ui-datepicker .ui-datepicker-header .ui-datepicker-next:focus::before, .ui-datepicker .ui-datepicker-header .ui-datepicker-prev:hover::before, .ui-datepicker .ui-datepicker-header .ui-datepicker-prev:focus::before {
	color: #343a40;
}
.ui-datepicker .ui-datepicker-header .ui-datepicker-next {
	order: 3;
}
.ui-datepicker .ui-datepicker-header .ui-datepicker-next:before {
	right: 5px;
	content: '\f105';
}
.ui-datepicker .ui-datepicker-header .ui-datepicker-prev:before {
	left: 5px;
	content: '\f104';
}
.ui-datepicker .ui-datepicker-header .ui-datepicker-next-hover, .ui-datepicker .ui-datepicker-header .ui-datepicker-prev-hover {
	color: #495057;
	cursor: pointer;
	top: 1px;
	border: 0;
	background-color: transparent;
}
.ui-datepicker .ui-datepicker-title {
	color: #5B93D3;
}
.ui-datepicker .ui-datepicker-calendar {
	margin: 0;
	background-color: transparent;
	border-bottom-right-radius: 3px;
	border-bottom-left-radius: 3px;
}
.ui-datepicker .ui-datepicker-calendar th {
	text-transform: uppercase;
	font-size: 10px;
	font-weight: 500;
	letter-spacing: 1px;
	padding: 6px 10px;
	color: #a3a7b7;
}
@media (max-width: 320px) {
	.ui-datepicker .ui-datepicker-calendar th {
		padding: 4px 0;
		letter-spacing: normal;
	}
	.work-progress h4{
		font-size:.85rem;
	}
	.work-progress h3{
		font-size:1.2rem;
	}
	.cal1 .clndr .clndr-controls .month {
		width: 33%;
		text-align: center;
		font-size: 12px;
		margin-left: 7px;
	}
	.clock-presenter{
		margin:0px 5px !important;
		width: 50px !important;
		height: 50px !important;
		border-radius: 50%;
		float: left;
		margin: 0 20px;
	}
	.countdown span:first-child{
		font-size:18px !important;
	}
	.countdown span:last-child {
		font-size:12px;
	}
	.chat .video-icon{
		display:none !important;
	}
	.chat .call-icon{
		display:none !important;
	}
	.construction {
		text-align:center !important;
	}
}
.ui-datepicker .ui-datepicker-calendar td {
	border: 1px solid #eceef9;
	padding: 0;
	background-color: #eceef9;
	text-align: right;
}
.ui-datepicker .ui-datepicker-calendar td:last-child {
	border-right: 0;
}
.ui-datepicker .ui-datepicker-calendar td.ui-datepicker-other-month .ui-state-default {
	color: #ccc;
}
.ui-datepicker .ui-datepicker-calendar td span, .ui-datepicker .ui-datepicker-calendar td a {
	transition: all 0.2s ease-in-out;
	padding: 5px;
	background-color: #fff;
	color: #473b52;
	padding: 6px 10px;
	display: block;
	font-weight: 400;
	font-size: 12px;
	border: 0;
	border-radius: 1px;
}
.ui-datepicker .ui-datepicker-calendar td a:hover {
	background-color: #f0f2f7;
	color: #473b52;
}
.ui-datepicker .ui-datepicker-calendar .ui-datepicker-today a {
	background-color: #f8f9fa;
	color: #473b52;
}
.ui-datepicker-multi .ui-datepicker-group {
	padding-right: 15px;
